        'practise_exploration_q1': '17.1 Hypothesizing entails expressing a business idea (or epic) in terms of the business value it is expected to deliver. This hypothesis is then implemented as a Minimum Viable Product (MVP) through the Continuous Delivery Pipeline and evaluated for accuracy when released. Rate your teams ability to translate business ideas into hypothesis statements that are clear and measurable.',
